import styles from "./AdminLoginPage.module.scss";
import { useState, useMemo, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import AuthPageContainer from "../../components/AuthPageContainer/AuthPageContainer.jsx";
import AuthInput from "../../components/AuthInput/AuthInput.jsx";
import Button from "../../components/Button/Button.jsx";
import { useAuthContext } from "../../contexts/AuthContext.jsx";
import Swal from "sweetalert2";

//{ labelName, type, value, placeholder, onChange, notification, lengthLimit }

export default function AdminLoginPage() {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const { login, isAuthenticated } = useAuthContext();

  const [msg, setMsg] = useState('')

  //useMemo
  // const isValid = useMemo(() => {
  //   if(!account) {
  //     return false
  //   }
  //   if(!password) {
  //     return false
  //   }
  //   return true
  // }, [account, password])

  //handleClick
  const handleClick = async () => {
    if (!account || !password) {
      Swal.fire({
        toast: true,
        position: "top",
        title: "",
        icon: "warning",
        timer: 1000,
        showConfirmButton: false,
      });
      setMsg("");
      // console.log("Check user info");
      return;
    }
    // if(!isValid) {
    //   console.log('Check your user info')
    //   return
    // }
    const success = await login({ 
      account, 
      password,
      role: 'admin',
    });
    if (success) {
      Swal.fire({
        toast: true,
        position: "top",
        title: "",
        icon: "success",
        timer: 1000,
        showConfirmButton: false,
      });
      // console.log("Login success");
      return;
    }
    //login failed
    Swal.fire({
      toast: true,
      position: "top",
      title: "!",
      icon: "error",
      timer: 1000,
      showConfirmButton: false,
    });
    setMsg("!");
    // console.log("LoginError");
  };

  // useEffect
  useEffect(() => {
    //
    if (isAuthenticated) {
      navigate("/admin/tweet");
    }
  }, [isAuthenticated, navigate]);

  return (
    <AuthPageContainer title=''>
      <AuthInput
        labelName=''
        type='text'
        value={account}
        placeholder=''
        onChange={(accountInput) => setAccount(accountInput)}
        notification={
          msg === "!" || msg === "" ? msg : 0
        }
        lengthLimit={50}
      />
      <AuthInput
        labelName=''
        type='password'
        value={password}
        placeholder=''
        onChange={(passwordInput) => setPassword(passwordInput)}
        notification={msg === "" ? msg : 0}
        lengthLimit={50}
      />
      <Button size='extraLarge' title='' onClick={handleClick} />

      {/* switch link */}
      <div className={styles.linkContainer}>
        <Link to='/login'>
          <span className={styles.link}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}
